/*-----------------------------------------------------------------*/
/*default iCore*/

*{
  -webkit-box-sizing : border-box;
          box-sizing : border-box;
}

*:before,
*:after{
  -webkit-box-sizing : border-box;
          box-sizing : border-box;
}

html{
               line-height : 1.3;
  -webkit-text-size-adjust : 100%;
        -webkit-box-sizing : border-box;
                box-sizing : border-box;
}

body{
            margin : 0;
       font-family : "Segoe UI", sans-serif;
             color : #444;
         font-size : 15px;
  background-color : #fff;
        overflow-y : auto;
        overflow-x : hidden;
}

hr {
  -webkit-box-sizing : content-box;
          box-sizing : content-box;
              height : 0;
            overflow : visible;
}

a{
  text-decoration : none;
            color : inherit;
 background-color : transparent;
}

p{
  word-wrap : break-word;
  margin    : 0;
  padding   : 0;
}

pre {
  font-family : monospace, monospace;
  font-size   : 14px;
}

code,
kbd,
samp {
  font-family : monospace, monospace;
  font-size   : 14px;
}

pre code{
  -webkit-overflow-scrolling : touch;
     -moz-osx-font-smoothing : auto;
            background-color : #fff;
                     display : block;
                     padding : 16px;
                       color : inherit;
                 font-family : monospace;
                 line-height : 1.3;
                    overflow : auto;
                      border : 1px solid #ddd;
                      margin : 16px 0;
               -moz-tab-size : 4;
                 -o-tab-size : 4;
                    tab-size : 4;
             -webkit-hyphens : none;
                 -ms-hyphens : none;
                     hyphens : none;
                 white-space : pre;
}

abbr[title] {
            border-bottom : none;
          text-decoration : underline;
  -webkit-text-decoration : underline dotted;
          text-decoration : underline dotted;
}

b,
strong {
  font-weight : bolder;
}

small {
  font-size : 80%;
}

sub,
sup {
       font-size : 75%;
     line-height : 0;
        position : relative;
  vertical-align : baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

h1{
  font-size : 50px;
}
h2{
  font-size : 42px;
}
h3{
  font-size : 35px;
}
h4{
  font-size : 28px;
}
h5{
  font-size : 21px;
}
h6{
  font-size : 16px;
}

h1,
h2,
h3,
h4,
h5,
h6{
       margin : 0;
  font-weight : bold;
  font-family : inherit;
        color : inherit;
}

img{
  max-width    : 100%;
  display      : block;
  border-style : none;
}

button,
input,
optgroup,
select,
textarea {
  font-family : inherit;
    font-size : 100%;
  line-height : 1.15;
       margin : 0;
}

button,
input {
  overflow : visible;
}

button,
select {
  text-transform : none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance : button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style : none;
       padding : 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline : 1px dotted ButtonText;
}

fieldset {
  padding : 0.35em 0.75em 0.625em;
}

legend {
  -webkit-box-sizing : border-box;
          box-sizing : border-box;
               color : inherit;
             display : table;
           max-width : 100%;
             padding : 0;
         white-space : normal;
}

progress {
  vertical-align : baseline;
}

textarea {
  overflow : auto;
}

[type="checkbox"],
[type="radio"] {
  -webkit-box-sizing : border-box;
          box-sizing : border-box;
             padding : 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height : auto;
}

[type="search"] {
  -webkit-appearance : textfield;
      outline-offset : -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance : none;
}

::-webkit-file-upload-button {
  -webkit-appearance : button;
                font : inherit;
}

details {
  display : block;
}

summary {
  display : list-item;
}

template {
  display : none;
}

[hidden] {
  display : none;
}

ul,
ol{
  padding : 0;
  margin  : 0;
}

li{
  list-style-type : none;
  padding         : 0;
  margin          : 0;
}

/*end default core*/
/*-----------------------------------------------------------------*/

/*-----------------------------------------------------------------*/
/*core grid*/

.grid-eq{
  padding : 8px;
}
.grid-eq-t{
  padding : 2px;
}
.grid-eq-s{
  padding : 4px;
}
.grid-eq-m{
  padding : 16px;
}
.grid-eq-l{
  padding : 24px;
}
.grid-eq-g{
  padding : 32px;
}

.row:after,
.row:before{
  display : table;
  content : " ";
    clear : both;
}

.column{
  float : left;
  width : 100%;
}

.column.s12{width:100%}
.column.s11{width:91.66666667%}
.column.s10{width:83.33333333%}
.column.s9{width:75%}
.column.s8{width:66.66666667%}
.column.s7{width:58.33333333%}
.column.s6{width:50%}
.column.s5{width:41.66666667%}
.column.s4{width:33.33333333%}
.column.s3{width:25%}
.column.s2{width:16.66666667%}
.column.s1{width:8.33333333%}

@media only screen and (min-width:601px) {
  .column.m12{width:100%}
  .column.m11{width:91.66666667%}
  .column.m10{width: 83.33333333%}
  .column.m9{width:75%}
  .column.m8{width:66.66666667%}
  .column.m7{width:58.33333333%}
  .column.m6{width:50%}
  .column.m5{width:41.66666667%}
  .column.m4{width:33.33333333%}
  .column.m3{width:25%}
  .column.m2{width:16.66666667%}
  .column.m1{width:8.33333333%}
}

@media only screen and (min-width:993px){
  .column.l12{width:100%}
  .column.l11{width:91.66666667%}
  .column.l10{width:83.33333333%}
  .column.l9{width:75%}
  .column.l8{width:66.66666667%}
  .column.l7{width:58.33333333%}
  .column.l6{width:50%}
  .column.l5{width:41.66666667%}
  .column.l4{width:33.33333333%}
  .column.l3{width:25%}
  .column.l2{width:16.66666667%}
  .column.l1{width:8.33333333%}
}

/*end core grid*/
/*-----------------------------------------------------------------*/

/*-----------------------------------------------------------------*/
/*Typography*/

.left-align {
  text-align: left;
}

.right-align {
  text-align: right;
}

.center-align {
  text-align: center;
}

.justify-align {
  text-align: justify;
}

/*end typography*/
/*-----------------------------------------------------------------*/

/*-----------------------------------------------------------------*/
/*position*/

.position-leftmiddle{
  position: absolute;
  top: 50%;
  left: 0%;
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}

.position-rightmiddle{
  position: absolute;
  top: 50%;
  right: 0%;
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}

.position-topmiddle{
  position: absolute;
  left: 50%;
  top: 0%;
  -webkit-transform: translate(-50%,0%);
  transform: translate(-50%,0%);
  text-align: center;
}

.position-bottommiddle{
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translate(-50%,0%);
  transform: translate(-50%,0%);
  text-align: center;
}

.position-middle{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  text-align: center;
}

.position-topleft{
  position: absolute;
  top: 0;
  left: 0;
}

.position-topright{
  position: absolute;
  top: 0;
  right: 0;
}

.position-bottomright{
  position: absolute;
  bottom: 0;
  right:0;
}

.position-bottomleft{
  position: absolute;
  bottom: 0;
  left: 0;
}

/*end position*/
/*-----------------------------------------------------------------*/

/*-----------------------------------------------------------------*/
/*layout*/

.layout{
  max-width: 1024px;
  margin: auto;
}
.layout-t{
  max-width: 640px;
  margin: auto;
}
.layout-s{
  max-width: 800px;
  margin: auto;
}
.layout-m{
  max-width: 1280px;
  margin: auto;
}
.layout-l{
  max-width: 1440px;
  margin: auto;
}
.layout-g{
  max-width: 1600px;
  margin: auto;
}

/*end layout*/
